.loader {
  position: relative;
  padding: 1.25rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.loader > * {
  position: absolute;
}

.loader svg {
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
}

.loader svg circle {
  fill: none;
  stroke: currentColor;
  stroke-width: 10%;
}

.loader svg {
  transform: rotate(-90deg);
}

.loader svg circle:first-of-type {
  stroke-dasharray: var(--loaded, 0), 100;
  transition: stroke-dasharray 0.4s cubic-bezier(0.04, 0.24, 0.18, 1);
}

